import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import {
  PageWrapper,
  HeaderWrapper,
  NextPrevArrow,
  HeaderName,
  ActualWeight,
  BilboardLabel,
  LastResultsBox,
  ButtonsWrapper,
} from './DoTraining.style';
import { useParams } from 'react-router-dom';
import { useAuth } from 'store/AuthContext';
import { useDatabase } from 'store/DatabaseContext';
import Button from 'components/atoms/Button/Button';
import Diagram from 'components/molecules/Diagram/Diagram';

const DoTraining = (props) => {
  const { training, day } = useParams();
  const { currentUser } = useAuth();
  const { getTrainingDay } = useDatabase();
  const [trainingDay, setTrainingDay] = useState([]);

  useEffect(() => {
    const getData = async () => {
      try {
        await getTrainingDay(currentUser.uid, setTrainingDay, training, day);
      } catch (error) {
        console.log(error);
      }
    };

    getData();
    return () => getData;
  }, []);
  console.log(trainingDay);

  return (
    <PageWrapper>
      <HeaderWrapper>
        <NextPrevArrow></NextPrevArrow>
        <HeaderName>
          {trainingDay.length && trainingDay[0].exerciseName}
        </HeaderName>
        <NextPrevArrow rotated></NextPrevArrow>
      </HeaderWrapper>
      <ActualWeight>
        {trainingDay.length && trainingDay[0].startWeight} kg
      </ActualWeight>
      <div>
        <BilboardLabel>średnia</BilboardLabel>
        <Diagram />
      </div>
      <div>
      <BilboardLabel>ostatnie wyniki</BilboardLabel>
        <LastResultsBox></LastResultsBox>
      </div>
      <ButtonsWrapper>
          <Button squere>+{trainingDay.length && trainingDay[0].basicProgression}</Button>
          <Button squere>=</Button>
          <Button squere>+/-?</Button>
      </ButtonsWrapper>
      <Button>Wróć</Button>
    </PageWrapper>
  );
};

DoTraining.propTypes = {};

export default DoTraining;
