import React from 'react';
import PropTypes from 'prop-types';
import { StyledDiagram } from './Diagram.style';
import bb, { area, areaSpline } from 'billboard.js';

const Diagram = (props) => {
  const chart = bb.generate({
    data: {
      columns: [['data', 45, 47, 50, 50, 47, 50]],
      types: {
        data: areaSpline(),
      },
    },
    axis: {
      y: {
        show: false,
        max: 55,
        min: 40,
      },
      x: {
        show: false,
        padding: {
          left: 0,
          right: 0,
        },
      },
    },
    legend: {
      show: false,
    },
    area: {
      linearGradient: true,
    },

    bindto: '#areaChart',
  });

  return <StyledDiagram id="areaChart"></StyledDiagram>;
};

Diagram.propTypes = {};

export default Diagram;
